<template>
  <div class="container">
    <h3>To-Do list</h3>
    <div class="wrapper-stat">
      <div class="stat business">
        <div class="h5">Business</div>
        <div class="wrapper-progress">
          <div class="bar" :style="{ width: `${statistics.business}` }"></div>
        </div>
      </div>
      <div class="stat">
        <div class="h5">Personal</div>
        <div class="wrapper-progress">
          <div
              class="bar personal"
              :style="{ width: `${statistics.personal}` }"
          ></div>
        </div>
      </div>
    </div>
    <div class="tasks">
      <div
          class="task"
          :class="{completed: task.isCompleted}"
          v-for="task in tasks"
          :key="task.id"
          @click="completedHendler(task.id)"
      >
        <div
            class="round"
            :class="{
         business: task.type === 'business',
        personal: task.type === 'personal'
      }"
        ></div>
        <span>{{ task.name }}</span>
        <div class="remove-task-icon" @click="removeTask(task.id)">
          <img src="../src/assets/bin.png" alt="remove"/>
        </div>
      </div>
    </div>
    <div class="card">
      <input
          type="text"
          placeholder="Название задачи"
          v-model="taskName"
          v-on:keyup.enter="addTask, addTaskTo"

      />
      <button @click="addTaskTo" :disabled="taskName.length === 0">+</button>
      <button @click="addTask" class="btn" :disabled="taskName.length === 0">+</button>
    </div>
  </div>
</template>

<script>
/*
[x]- Add task when enter key
[x]- Clear input when added task
[x]- delete task
[x]- Progress bar business & personal tasks

[*]- Choice color in input
[-]- Write to DB
[-]- View from DB
[-]- Fix error isComleted undef
[-]- Разделить на компоненты
*/

export default {
  name: 'App',
data() {
  return {
    taskName: '',
    tasks: [
      {
      id: 'qqwweerr',
      name: 'Создать ToDo на Vue',
      isCompleted: true,
      type: 'business'
    },
      {
        id: 'qqwweerrff',
        name: 'Отправить проект на проверку',
        isCompleted: false,
        type: 'personal'
      }]
  }
},
  methods: {
    completedHendler: function (taskId) {
      const currentTask = this.tasks.find((t) => t.id === taskId)
      currentTask.isCompleted = !currentTask.isCompleted
    },
    addTask: function () {
      this.tasks.push({
        id: Math.random()
            .toString(36)
            .substring(2, 7),
        name: this.taskName,
        isCompleted: false,
        type: 'personal'
      })
      this.taskName = ''
    },
    addTaskTo: function () {
      this.tasks.push({
        id: Math.random()
            .toString(36)
            .substring(2, 7),
        name: this.taskName,
        isCompleted: false,
        type: 'business'
      })
      this.taskName = ''
    },
    removeTask: function (taskId) {
      this.tasks = this.tasks.filter(t => t.id !== taskId)
    }
  },
  computed: {
    statistics: function() {
      const count = this.tasks.length || 1
      const businessData = this.tasks.filter(t => t.type === 'business')
      const personalData = this.tasks.filter(t => t.type === 'personal')

      return {
        business: Math.round((businessData.length * 100) / count) + '%',
        personal: Math.round((personalData.length * 100) / count) + '%',
      }
    }
  }
}

</script>


